import React from "react";

const NotFound = () => {
  return (
    <div>
      <h1>Error 404(Not found)</h1>
      <h6>Sorry,the page you are looking for does not exist</h6>
      <p>It might be that you typed something incorrectly</p>

    </div>
  );
};

export default NotFound;



